.carousel-home {

	width: 100vw;
	position: relative;
	position: fixed;
	top: 0px;
	left: 0px;
	opacity: 1;
	background-color: transparent;
	background-size: 100%;
}

.carousel-image-home {
	background-position: top;
	background-size: cover;
	height: 100vh;
	width: 100vw;
	z-index: 5;
	opacity: 1;
}

#prev {
	position: absolute;
	top: 5vh;
	height: 95vh;
	z-index: 8;
}

#next {
	position: absolute;
	top: 5vh;
	height: 95vh;
	z-index: 5;
}
